@import '../../scss';

:host {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  phn-p-link-pure,
  phn-p-button-pure,
  phn-menu-item {
    box-sizing: border-box;
    padding: 0 8px;

    &.models {
      padding: p-px-to-rem(16px) p-px-to-rem(8px) p-px-to-rem(16px) p-px-to-rem(14px);
    }

    .body-container {
      height: p-px-to-rem(52px);
    }

    phn-p-text {
      font-size: p-px-to-rem(20px);

      @include pds-media-query-min('l') {
        font-size: p-px-to-rem(22px);
      }

      @include pds-media-query-min('xxl') {
        font-size: p-px-to-rem(24px);
      }
    }

    .body-container {
      max-width: 230px;

      @include pds-media-query-min('l') {
        max-width: 270px;
      }

      @include pds-media-query-min('xxl') {
        max-width: 400px;
      }

      .text-container {
        width: 100%;
      }
    }
  }

  .model-signature {
    height: p-px-to-rem(26px);

    @include pds-media-query-min('xxl') {
      height: p-px-to-rem(30px);
    }
  }

  div.text-limit {
    @include pds-media-query-min('m') {
      width: 220px;
    }

    @include pds-media-query-min('l') {
      width: 265px;
    }
  }
}
