import { TDataLayer } from '@porsche/globaldatalayer-events/projects/pcom/all/types';
import { Component, Element, h, Host, Prop } from '@stencil/core';
import analyticsManager from '../../services/analytics/analytics-manager';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';
import { ClickElementType, ClickElementTypes, ElementTypes, EventAction, EventActions } from '../../utility/constants';
import { isModelName, isString } from '../../utility/helper';
import SideDrawerItemText from './side-drawer-item-text';
import { navContentStore } from '../../state/nav-content-store';

@Component({
  tag: 'phn-side-drawer-item',
  styleUrl: 'side-drawer-item.scss',
  scoped: true
})
export class SideDrawerItem {
  @Prop() text!: string;
  @Prop() analyticsId!: string;
  @Prop() subtext = '';
  @Prop() elementId = '';
  @Prop() link = '';
  @Prop() iconRight = '';
  @Prop() datalayerEventAction: EventAction = EventActions.NAVIGATION_CLICK;
  @Prop() customDatalayerProperties: TDataLayer = {};
  @Prop() isActive = false;
  @Prop() hasDoubleDrawerAnimationFinished = false;
  @Prop() renderSignatures = false;

  @Element() el!: HTMLPhnSideDrawerItemElement;

  private sendDataLayerEvent(clickElementType: ClickElementType, elementId: string): void {
    globalDataLayerService.pushDataLayerEvent(this.datalayerEventAction, {
      componentClick: {
        clickElementId: this.analyticsId,
        clickElementName: this.text,
        targetElement: analyticsManager.getModelTargetElement(elementId, this.text),
        clickElementType,
        ...(clickElementType === ClickElementTypes.Link ? { targetUrl: this.link } : {})
      },
      ...this.customDatalayerProperties,
      componentDisplay: {
        displayElementId: analyticsManager.calculateDisplayElementId(),
        displayElementType: analyticsManager.calculateDisplayElementType(elementId, ElementTypes.sideDrawerItem, true),
        displayElementName: this.text
      },
      ...(this.datalayerEventAction === EventActions.MODEL_RANGE_CLICK
        ? {
            car: {
              modelRangeName: this.text.toLowerCase()
            }
          }
        : {})
    });
  }

  private existsSlot(slotName: string): boolean {
    return this.el.querySelector(`[slot="${slotName}"]`) !== null;
  }

  render(): HTMLPhnSideDrawerItemElement {
    const body = (
      <div class="body-container phn-flex-row phn-align-center">
        <SideDrawerItemText
          isActive={false}
          text={this.text}
          subtext={this.subtext}
          renderSignatures={this.renderSignatures}
        />
        {this.existsSlot('suffix') ? (
          <div>
            <slot name="suffix" />
          </div>
        ) : null}
      </div>
    );

    if (isString(this.link)) {
      const externalShop = navContentStore.state.externalShop?.link === this.link;

      return (
        <phn-p-link-pure
          icon={isString(this.iconRight) ? this.iconRight : 'none'}
          align-label="left"
          stretch="true"
          data-id={this.elementId}
          onClick={() => {
            this.sendDataLayerEvent(ClickElementTypes.Link, this.elementId);
          }}
        >
          <a href={this.link} target={externalShop ? '_blank' : '_self'}>
            {body}
          </a>
        </phn-p-link-pure>
      );
    }

    return (
      <Host class={`${this.isActive ? 'active' : ''}`}>
        <phn-p-button-pure
          active={this.isActive}
          class={isModelName(this.text) ? 'models' : ''}
          size="inherit"
          stretch="true"
          data-id={this.elementId}
          onClick={() => {
            this.sendDataLayerEvent(ClickElementTypes.Button, this.elementId);
          }}
          aria={{ 'aria-expanded': `${this.isActive ? 'true' : 'false'}` }}
          icon={'arrow-head-right'}
          align-label="left"
        >
          {body}
        </phn-p-button-pure>
      </Host>
    );
  }
}
